(() => {
  window.enviarEmailConfirmacion = async (datosTurno) => {
    try {
      const emailData = {
        senderName: window.brevoConfig.senderName,
        senderEmail: window.brevoConfig.senderEmail,
        to: [{ email: datosTurno.email, name: datosTurno.nombre }],
        subject: `Confirmación de turno - ${datosTurno.fecha} a las ${datosTurno.horario}`,
        htmlContent: `
          <!DOCTYPE html>
          <html>
          <head>
              <style>
                  body { font-family: 'Poppins', sans-serif; line-height: 1.6; color: #333; }
                  .container { max-width: 600px; margin: 0 auto; padding: 20px; }
                  .header { background-color: #f8f1e9; padding: 20px; text-align: center; }
                  .content { padding: 20px; }
                  .footer { text-align: center; font-size: 12px; color: #777; margin-top: 20px; }
                  .resumen { margin-top: 20px; border-top: 1px solid #eee; padding-top: 20px; }
                  .servicio { margin-bottom: 5px; }
              </style>
          </head>
          <body>
              <div class="container">
                  <div class="header">
                      <h2>Rezerva Peluquería</h2>
                      <p>¡Tu turno ha sido confirmado!</p>
                  </div>
                  
                  <div class="content">
                      <p>Hola <strong>${datosTurno.nombre}</strong>,</p>
                      <p>Tu turno ha sido reservado con éxito. A continuación los detalles:</p>
                      
                      <div class="resumen">
                          <h3>Resumen de tu reserva</h3>
                          <p><strong>Fecha:</strong> ${datosTurno.fecha}</p>
                          <p><strong>Hora:</strong> ${datosTurno.horario}</p>
                          <p><strong>Servicios:</strong></p>
                          <ul>
                              ${datosTurno.servicios.map(s => `<li class="servicio">${s}</li>`).join('')}
                          </ul>
                          <p><strong>Seña abonada:</strong> $${datosTurno.reserva.toLocaleString()}</p>
                          <p><strong>Saldo pendiente:</strong> $${(datosTurno.montoTotal - datosTurno.reserva).toLocaleString()}</p>
                      </div>
                      
                      <p>Si necesitas modificar o cancelar tu turno, por favor contáctanos respondiendo este email.</p>
                  </div>
                  
                  <div class="footer">
                      <p>© ${new Date().getFullYear()} Rezerva Peluquería. Todos los derechos reservados.</p>
                  </div>
              </div>
          </body>
          </html>
        `
      };

      // Opcional: enviar copia al admin
      if (window.brevoConfig.adminEmail) {
        emailData.bcc = [{ email: window.brevoConfig.adminEmail, name: 'Administrador Rezerva' }];
      }

      // Llamar a la Cloud Function
      const enviarEmail = firebase.functions().httpsCallable('enviarEmailConfirmacion');
      const resultado = await enviarEmail(emailData);
      
      return resultado.data.success;
    } catch (error) {
      console.error('Error en enviarEmailConfirmacion:', error);
      return false;
    }
  };
})();