const ADMIN_UID = 'lnYDaOVZYOZLi9GyDdomUOZBf5p1';

// Elementos del DOM
const elements = {
  authContainer: document.getElementById('auth-container'),
  adminPanel: document.getElementById('admin-panel'),
  loginBtn: document.getElementById('btn-admin-login'),
  logoutBtn: document.getElementById('logout-btn'),
  calendar: document.getElementById('calendar'),
  turnosList: document.getElementById('turnos-list'),
  fechaTitulo: document.getElementById('fecha-titulo'),
  modalEditar: document.getElementById('modal-editar'),
  formEditar: document.getElementById('form-editar'),
  btnEliminar: document.querySelector('.btn-eliminar'),
  btnEliminarModal: document.getElementById('btn-eliminar-modal'),
  closeModal: document.querySelector('.close-modal')
};

let calendar;
let currentTurnoId = null;

async function verificarFirestore() {
  try {
    const config = await obtenerConfiguracion();
    // console.log("Configuración cargada correctamente:", config);
    return true;
  } catch (error) {
    console.error("Error conectando con Firestore:", error);
    alert("Error al conectar con la base de datos. Recargando...");
    setTimeout(() => location.reload(), 3000);
    return false;
  }
}

async function initAdmin() {

  if (!(await verificarFirestore())) return;
  
  if (!elements.authContainer) return;

  firebase.auth().onAuthStateChanged(handleAuthState);
  
  if (elements.loginBtn) {
    elements.loginBtn.addEventListener('click', signInWithGoogle);
  }
  
  if (elements.logoutBtn) {
    elements.logoutBtn.addEventListener('click', signOut);
  }

  // Eventos del modal
  if (elements.closeModal) {
    elements.closeModal.addEventListener('click', () => {
      elements.modalEditar.style.display = 'none';
    });
  }

  if (elements.formEditar) {
    elements.formEditar.addEventListener('submit', async (e) => {
      e.preventDefault();
      await guardarCambiosTurno();
    });
  }

  // Eliminar cualquier listener previo del botón eliminar
  const btnEliminarModal = document.getElementById('btn-eliminar-modal');
  if (btnEliminarModal) {
    btnEliminarModal.onclick = null;
  }

  // Cerrar modal al hacer click fuera
  window.addEventListener('click', (e) => {
    if (e.target === elements.modalEditar) {
      elements.modalEditar.style.display = 'none';
    }
  });
}

// Agregar estas funciones al inicio del admin.js
async function obtenerConfiguracion() {
  try {
    const doc = await db.collection('config').doc('horarios').get();
    if (!doc.exists) return obtenerConfiguracionPorDefecto();
    
    const data = doc.data();
    
    // Función robusta para normalizar días laborales
    const normalizarDias = (dias) => {
      if (!dias) return [1,2,3,4,5];
      
      // Caso: array con string JSON ['[1,2,3,4,5]']
      if (Array.isArray(dias) && dias.length === 1 && typeof dias[0] === 'string') {
        try {
          return JSON.parse(dias[0].replace(/'/g, '"')).map(Number);
        } catch (e) {
          console.warn("Error parseando array con string JSON", e);
        }
      }
      
      // Caso: string "[1,2,3,4,5]"
      if (typeof dias === 'string' && dias.startsWith('[')) {
        try {
          return JSON.parse(dias.replace(/'/g, '"')).map(Number);
        } catch (e) {
          console.warn("Error parseando string JSON", e);
        }
      }
      
      // Caso: array de números [1,2,3,4,5]
      if (Array.isArray(dias)) return dias.map(Number);
      
      // Caso: string simple "1,2,3,4,5"
      if (typeof dias === 'string') {
        return dias.split(',').map(Number).filter(n => !isNaN(n));
      }
      
      return [1,2,3,4,5]; // Default
    };
    
    const diasLaborales = normalizarDias(data.diasLaborales)
      .filter(n => n >= 0 && n <= 6) // Filtrar valores inválidos
      .sort((a, b) => a - b); // Ordenar
    
    return {
      horarioApertura: data.horarioApertura || "09:00",
      horarioCierre: data.horarioCierre || "19:00",
      duracionTurno: data.duracionTurno || 30,
      diasLaborales: diasLaborales.length > 0 ? diasLaborales : [1,2,3,4,5]
    };
  } catch (error) {
    console.error("Error obteniendo configuración:", error);
    return obtenerConfiguracionPorDefecto();
  }
}

function obtenerConfiguracionPorDefecto() {
  return {
    horarioApertura: "09:00",
    horarioCierre: "19:00",
    duracionTurno: 30,
    diasLaborales: [1,2,3,4,5] // Lunes a Viernes
  };
}

function validarHorario(horario, config) {
  // Convertir horario a minutos desde medianoche
  const [horas, minutos] = horario.split(':').map(Number);
  const horarioEnMinutos = horas * 60 + minutos;
  
  // Convertir horarios de apertura/cierre
  const [aperturaH, aperturaM] = config.horarioApertura.split(':').map(Number);
  const [cierreH, cierreM] = config.horarioCierre.split(':').map(Number);
  const aperturaEnMinutos = aperturaH * 60 + aperturaM;
  const cierreEnMinutos = cierreH * 60 + cierreM;
  
  // Validar que esté dentro del horario laboral
  if (horarioEnMinutos < aperturaEnMinutos || horarioEnMinutos >= cierreEnMinutos) {
    return {
      valido: false,
      mensaje: `El horario debe estar entre ${config.horarioApertura} y ${config.horarioCierre}`
    };
  }
  
  // Validar que sea en intervalos de 30 minutos
  if (minutos % config.duracionTurno !== 0) {
    return {
      valido: false,
      mensaje: `Los turnos deben ser en intervalos de ${config.duracionTurno} minutos (ej: 09:00, 09:30, etc.)`
    };
  }
  
  return { valido: true };
}

async function handleAuthState(user) {
  if (user && user.uid === ADMIN_UID) {
    await showAdminPanel();
    await initCalendar();
    await loadTurnosForDate(new Date().toISOString().split('T')[0]);
  } else {
    showLoginScreen();
  }
}

async function showAdminPanel() {
  if (elements.authContainer) elements.authContainer.style.display = 'none';
  if (elements.adminPanel) elements.adminPanel.style.display = 'block';
}

function showLoginScreen() {
  if (elements.authContainer) elements.authContainer.style.display = 'flex';
  if (elements.adminPanel) elements.adminPanel.style.display = 'none';
  if (calendar) calendar.destroy();
}

async function signInWithGoogle() {
  try {
    const provider = new firebase.auth.GoogleAuthProvider();
    await firebase.auth().signInWithPopup(provider);
  } catch (error) {
    console.error("Error de autenticación:", error);
    alert("Error al iniciar sesión: " + error.message);
  }
}

async function signOut() {
  try {
    await firebase.auth().signOut();
  } catch (error) {
    console.error("Error al cerrar sesión:", error);
  }
}

async function initCalendar() {
  if (!elements.calendar) return;
  
  calendar = new FullCalendar.Calendar(elements.calendar, {
    initialView: 'dayGridMonth',
    locale: 'es',
    headerToolbar: {
      left: 'prev,next today',
      center: 'title',
      right: 'none'
    },
    selectable: true,
    selectMirror: true,
    dayMaxEvents: true,
    selectOverlap: false,
    
    dateClick: async function(info) {
      // Remover la clase de selección de todas las celdas primero
      document.querySelectorAll('.fc-daygrid-day-selected').forEach(el => {
        el.classList.remove('fc-daygrid-day-selected');
      });
      
      // Añadir la clase de selección a la celda clickeada
      info.dayEl.classList.add('fc-daygrid-day-selected');
      
      // Cargar los turnos para la fecha seleccionada
      await loadTurnosForDate(info.dateStr);
    },
    
    events: async function(fetchInfo, successCallback, failureCallback) {
      try {
        const snapshot = await db.collection('turnos')
          .where('fecha', '>=', fetchInfo.startStr)
          .where('fecha', '<=', fetchInfo.endStr)
          .get();
        
        const events = snapshot.docs.map(doc => {
          const data = doc.data();
          return {
            id: doc.id,
            title: `${data.cliente} - ${data.servicios.join(', ')}`,
            start: `${data.fecha}T${data.horario}`,
            allDay: false,
            extendedProps: {
              cliente: data.cliente,
              telefono: data.telefono,
              email: data.email,
              servicios: data.servicios,
              observaciones: data.observaciones,
              estado: data.estado || 'pendiente'
            },
            className: `fc-event-${data.estado || 'pendiente'}`,
            borderColor: 'transparent'
          };
        });
        
        successCallback(events);
      } catch (error) {
        console.error("Error cargando eventos:", error);
        failureCallback(error);
      }
    },
    
    eventClick: function(info) {
      abrirModalEdicion(info.event);
    },
    
    select: function(info) {
      calendar.unselect();
    },
    
    dayMaxEventRows: false,
    views: {
      dayGridMonth: {
        dayMaxEventRows: false,
        displayEventTime: false
      },
      timeGridWeek: {
        dayMaxEventRows: true
      },
      timeGridDay: {
        dayMaxEventRows: true
      }
    },
    
    // Limpiar selección al cambiar de vista
    viewDidMount: function() {
      document.querySelectorAll('.fc-daygrid-day-selected').forEach(el => {
        el.classList.remove('fc-daygrid-day-selected');
      });
    }
  });
  
  calendar.render();
}

function getEventColor(estado) {
  switch(estado) {
    case 'confirmado': return '#4CAF50';
    case 'cancelado': return '#F44336';
    case 'completado': return '#2196F3';
    default: return '#FF9800';
  }
}

async function loadTurnosForDate(date) {
  try {
    // Corregir formato de fecha para el título
    const fechaObj = new Date(date);
    // Ajustar por zona horaria
    const fechaAjustada = new Date(fechaObj.getTime() + fechaObj.getTimezoneOffset() * 60000);
    
    const options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' };
    elements.fechaTitulo.textContent = `Turnos para ${fechaAjustada.toLocaleDateString('es-AR', options)}`;
    
    const snapshot = await db.collection('turnos')
      .where('fecha', '==', date)
      .orderBy('horario', 'asc')
      .get();
    
    renderTurnos(snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() })));
  } catch (error) {
    console.error("Error cargando turnos:", error);
    elements.turnosList.innerHTML = '<p>Error al cargar los turnos. Intente nuevamente.</p>';
  }
}

function renderTurnos(turnos) {
  if (!elements.turnosList) return;
  
  if (turnos.length === 0) {
    elements.turnosList.innerHTML = '<p class="sin-turnos">No hay turnos para esta fecha.</p>';
    return;
  }
  
  elements.turnosList.innerHTML = turnos.map(turno => `
    <div class="turno-card" data-id="${turno.id}">
      <div class="turno-header" data-id="${turno.id}">
        <div class="turno-sumario">
          <span class="turno-horario">${turno.horario}</span>
          <span class="turno-cliente">${turno.cliente}</span>
          <span class="turno-estado ${turno.estado || 'pendiente'}">${turno.estado || 'pendiente'}</span>
        </div>
        <span class="toggle-icon">▼</span>
      </div>
      <div class="turno-detalles">
        <div class="turno-info">
          <p><strong>Servicios:</strong> ${turno.servicios.join(', ')}</p>
          <p><strong>Teléfono:</strong> ${turno.telefono}</p>
          <p><strong>Email:</strong> ${turno.email}</p>
          ${turno.observaciones ? `<p><strong>Observaciones:</strong> ${turno.observaciones}</p>` : ''}
        </div>
        <div class="turno-actions">
          <button class="btn-editar" data-id="${turno.id}">Editar</button>
          <button class="btn-eliminar-lista" data-id="${turno.id}">Eliminar</button>
        </div>
      </div>
    </div>
  `).join('');
  
  // Configurar eventos para expandir/colapsar
  document.querySelectorAll('.turno-header').forEach(header => {
    header.addEventListener('click', function(e) {
      // Evitar que se active si se hace click en un botón de acción
      if (e.target.tagName === 'BUTTON') return;
      
      const card = this.closest('.turno-card');
      const detalles = card.querySelector('.turno-detalles');
      const icon = this.querySelector('.toggle-icon');
      
      card.classList.toggle('expanded');
      detalles.style.display = card.classList.contains('expanded') ? 'block' : 'none';
      icon.textContent = card.classList.contains('expanded') ? '▲' : '▼';
    });
  });
  
  // Configurar eventos para los botones eliminar (existente)
  document.querySelectorAll('.btn-eliminar-lista').forEach(btn => {
    btn.addEventListener('click', async (e) => {
      e.stopPropagation();
      const turnoId = e.currentTarget.getAttribute('data-id');
      
      if (confirm('¿Estás seguro de eliminar este turno?')) {
        const loading = mostrarLoading();
        try {
          await eliminarTurno(turnoId);
          const fechaActual = new Date().toISOString().split('T')[0];
          await loadTurnosForDate(fechaActual);
        } catch (error) {
          console.error("Error eliminando desde lista:", error);
          alert("Error al eliminar el turno");
        } finally {
          loading.remove();
        }
      }
    });
  });
  
  // Configurar eventos para los botones editar (existente)
  document.querySelectorAll('.btn-editar').forEach(btn => {
    btn.addEventListener('click', async (e) => {
      e.stopPropagation();
      const turnoId = e.currentTarget.getAttribute('data-id');
      await abrirModalEdicionPorId(turnoId);
    });
  });
}

async function abrirModalEdicion(event) {
  try {
    const config = await obtenerConfiguracion();
    await generarOpcionesHorario(config);
    
    currentTurnoId = event.id;
    
    // Llenar solo los campos necesarios
    document.getElementById('turno-id').value = event.id;
    document.getElementById('edit-fecha').value = event.start.toISOString().split('T')[0];
    
    // Formatear hora (HH:MM)
    const hora = event.start.getHours().toString().padStart(2, '0');
    const minutos = event.start.getMinutes().toString().padStart(2, '0');
    document.getElementById('edit-horario').value = `${hora}:${minutos}`;
    
    // Mostrar información del turno en el modal
    document.getElementById('turno-info').innerHTML = `
      <p><strong>Cliente:</strong> ${event.extendedProps.cliente}</p>
      <p><strong>Servicios:</strong> ${event.extendedProps.servicios.join(', ')}</p>
      <p><strong>Teléfono:</strong> ${event.extendedProps.telefono}</p>
      <p><strong>Estado:</strong> ${event.extendedProps.estado || 'pendiente'}</p>
    `;
    
    elements.modalEditar.style.display = 'block';
  } catch (error) {
    console.error("Error abriendo modal de edición:", error);
    alert("Error al cargar los datos del turno");
  }
}

async function abrirModalEdicionPorId(turnoId) {
  try {
    const config = await obtenerConfiguracion();
    await generarOpcionesHorario(config);
    
    const doc = await db.collection('turnos').doc(turnoId).get();
    if (!doc.exists) {
      alert('Turno no encontrado');
      return;
    }
    
    const turno = doc.data();
    currentTurnoId = turnoId;
    
    // Llenar solo los campos necesarios para el formulario simplificado
    document.getElementById('turno-id').value = turnoId;
    document.getElementById('edit-fecha').value = turno.fecha;
    document.getElementById('edit-horario').value = turno.horario;
    
    // Mostrar información del turno en el modal
    document.getElementById('turno-info').innerHTML = `
      <p><strong>Cliente:</strong> ${turno.cliente}</p>
      <p><strong>Servicios:</strong> ${Array.isArray(turno.servicios) ? turno.servicios.join(', ') : turno.servicios}</p>
      <p><strong>Teléfono:</strong> ${turno.telefono}</p>
      <p><strong>Estado:</strong> ${turno.estado || 'pendiente'}</p>
    `;
    
    // Configurar el botón eliminar del modal
    const btnEliminarModal = document.getElementById('btn-eliminar-modal');
    if (btnEliminarModal) {
      btnEliminarModal.onclick = async (e) => {
        e.stopPropagation();
        e.preventDefault();
        
        if (confirm('¿Estás seguro de eliminar este turno permanentemente?')) {
          try {
            const success = await eliminarTurno(currentTurnoId);
            if (success) {
              // Actualizar la vista
              await loadTurnosForDate(turno.fecha);
              calendar.refetchEvents();
              elements.modalEditar.style.display = 'none';
              alert('Turno eliminado correctamente');
            }
          } catch (error) {
            console.error("Error al eliminar turno:", error);
            alert("Error al eliminar el turno: " + error.message);
          }
        }
      };
    }
    
    elements.modalEditar.style.display = 'block';
  } catch (error) {
    console.error("Error abriendo modal de edición:", error);
    alert("Error al cargar los datos del turno");
  }
}

function configurarModalEliminar() {
  const btnEliminarModal = document.getElementById('btn-eliminar-modal');
  if (!btnEliminarModal) return;

  // Limpiar eventos previos
  btnEliminarModal.onclick = null;
  
  // Asignar nuevo evento
  btnEliminarModal.onclick = async (e) => {
    e.preventDefault();
    e.stopPropagation();
    
    if (!currentTurnoId) {
      console.error('No hay turno seleccionado');
      return;
    }

    if (confirm('¿Estás seguro de eliminar este turno permanentemente?')) {
      const loadingIndicator = mostrarLoading();
      try {
        await eliminarTurno(currentTurnoId);
      } catch (error) {
        console.error("Error en eliminación:", error);
      } finally {
        loadingIndicator.remove();
      }
    }
  };
}

function mostrarLoading() {
  const loading = document.createElement('div');
  loading.className = 'loading-indicator';
  loading.textContent = 'Procesando...';
  document.body.appendChild(loading);
  return loading;
}

async function generarOpcionesHorario(config) {
  const selectHorario = document.getElementById('edit-horario');
  selectHorario.innerHTML = '';
  
  // Convertir horarios de apertura/cierre a minutos
  const [aperturaH, aperturaM] = config.horarioApertura.split(':').map(Number);
  const [cierreH, cierreM] = config.horarioCierre.split(':').map(Number);
  
  let horaActual = aperturaH;
  let minutoActual = aperturaM;
  
  while (horaActual < cierreH || (horaActual === cierreH && minutoActual < cierreM)) {
    const horaStr = horaActual.toString().padStart(2, '0');
    const minutoStr = minutoActual.toString().padStart(2, '0');
    const option = document.createElement('option');
    option.value = `${horaStr}:${minutoStr}`;
    option.textContent = `${horaStr}:${minutoStr}`;
    selectHorario.appendChild(option);
    
    // Avanzar el tiempo
    minutoActual += config.duracionTurno;
    if (minutoActual >= 60) {
      horaActual += Math.floor(minutoActual / 60);
      minutoActual = minutoActual % 60;
    }
  }
}

async function verificarDisponibilidad(fecha, horario, turnoId = null) {
  try {
    const config = await obtenerConfiguracion();
    
    // Convertir horario a minutos
    const [hora, minuto] = horario.split(':').map(Number);
    const inicioEnMinutos = hora * 60 + minuto;
    const finEnMinutos = inicioEnMinutos + config.duracionTurno;
    
    // Convertir a formato HH:MM para la consulta
    const horarioFin = `${Math.floor(finEnMinutos / 60).toString().padStart(2, '0')}:${(finEnMinutos % 60).toString().padStart(2, '0')}`;
    
    // Consultar todos los turnos para la fecha
    let query = db.collection('turnos').where('fecha', '==', fecha);
    
    const snapshot = await query.get();
    
    // Filtrar localmente los turnos que se solapan
    const turnosSolapados = snapshot.docs.filter(doc => {
      // Si estamos editando, excluir el turno actual
      if (turnoId && doc.id === turnoId) return false;
      
      const turno = doc.data();
      const [turnoHora, turnoMinuto] = turno.horario.split(':').map(Number);
      const turnoInicio = turnoHora * 60 + turnoMinuto;
      const turnoFin = turnoInicio + config.duracionTurno;
      
      // Verificar solapamiento
      return (inicioEnMinutos < turnoFin && finEnMinutos > turnoInicio);
    });
    
    return {
      disponible: turnosSolapados.length === 0,
      mensaje: turnosSolapados.length > 0 ? `El horario se solapa con otro turno existente` : null
    };
  } catch (error) {
    console.error("Error verificando disponibilidad:", error);
    return {
      disponible: false,
      mensaje: "Error al verificar disponibilidad"
    };
  }
}

async function guardarCambiosTurno() {
  if (!currentTurnoId) {
    console.error('currentTurnoId no definido');
    return;
  }

  try {
    const user = firebase.auth().currentUser;
    if (!user || user.uid !== ADMIN_UID) {
      alert('No tienes permisos para realizar esta acción');
      return;
    }

    const config = await obtenerConfiguracion();
    const horario = document.getElementById('edit-horario').value;
    const fechaInput = document.getElementById('edit-fecha').value;
    
    // Debug: Mostrar información de validación
    // console.log("Validando turno para:", fechaInput, horario);
    // console.log("Configuración actual:", config);

    // Validar horario
    const validacionHorario = validarHorario(horario, config);
    if (!validacionHorario.valido) {
      alert(validacionHorario.mensaje);
      return;
    }
    
    // Crear fecha sin problemas de zona horaria (formato YYYY-MM-DD)
    const [year, month, day] = fechaInput.split('-').map(Number);
    const fechaObj = new Date(year, month - 1, day);
    
    // Obtener día de la semana (0=Domingo, 1=Lunes, ..., 6=Sábado)
    const diaSemana = fechaObj.getDay();
    
    // Debug: Verificar valores
    // console.log("Fecha seleccionada:", fechaInput);
    // console.log("Día de la semana calculado:", diaSemana);
    // console.log("Días laborales configurados:", config.diasLaborales);
    
    // Validar día laboral
    if (!config.diasLaborales.includes(diaSemana)) {
      const diasNombres = ['Domingo', 'Lunes', 'Martes', 'Miércoles', 'Jueves', 'Viernes', 'Sábado'];
      alert(`No se pueden agendar turnos los ${diasNombres[diaSemana]}s`);
      return;
    }
    
    // Verificar disponibilidad
    const disponibilidad = await verificarDisponibilidad(fechaInput, horario, currentTurnoId);
    if (!disponibilidad.disponible) {
      alert(disponibilidad.mensaje);
      return;
    }
    
    // Solo actualizar fecha y horario
    const turnoActualizado = {
      fecha: fechaInput,
      horario: horario,
      actualizadoEl: firebase.firestore.FieldValue.serverTimestamp()
    };
    
    await db.collection('turnos').doc(currentTurnoId).update(turnoActualizado);
    
    // Actualizar la vista
    await loadTurnosForDate(fechaInput);
    if (calendar) {
      calendar.refetchEvents();
    }
    elements.modalEditar.style.display = 'none';
    
    alert('Turno actualizado correctamente');
  } catch (error) {
    console.error("Error actualizando turno:", error);
    alert("Error al actualizar el turno: " + error.message);
  }
}

async function eliminarTurno(turnoId) {
  // Validación mejorada del ID
  if (!turnoId || typeof turnoId !== 'string') {
    console.error('ID de turno inválido:', turnoId);
    return false;
  }

  try {
    // console.log('Iniciando eliminación para ID:', turnoId);
    
    // Obtener la fecha del turno antes de eliminarlo
    const doc = await db.collection('turnos').doc(turnoId).get();
    if (!doc.exists) {
      alert('El turno ya no existe');
      return false;
    }
    
    const fechaTurno = doc.data().fecha;
    
    // Eliminar el documento
    await db.collection('turnos').doc(turnoId).delete();
    // console.log('Turno eliminado con éxito');
    
    // Actualizar la vista
    await loadTurnosForDate(fechaTurno);
    
    // Refrescar calendario
    if (calendar) {
      calendar.refetchEvents();
    }
    
    // Cerrar modal si está abierto
    if (elements.modalEditar) {
      elements.modalEditar.style.display = 'none';
    }
    
    return true;
  } catch (error) {
    console.error('Error al eliminar:', error);
    alert('Error al eliminar el turno: ' + (error.message || 'Por favor intente nuevamente'));
    throw error;
  }
}

function setupModalEvents() {
  const modal = document.getElementById('modal-editar');
  const btnEliminar = document.getElementById('btn-eliminar-modal');
  
  if (btnEliminar) {
    btnEliminar.onclick = async (e) => {
      e.stopPropagation(); // Detener la propagación
      e.preventDefault(); // Prevenir comportamiento por defecto
      
      if (!currentTurnoId) {
        console.error('No hay turno seleccionado');
        return;
      }

      if (confirm('¿Estás seguro de eliminar este turno permanentemente?')) {
        try {
          const success = await eliminarTurno(currentTurnoId);
          if (success) {
            // Actualizar la vista
            const fechaActual = new Date().toISOString().split('T')[0];
            await loadTurnosForDate(fechaActual);
            calendar.refetchEvents();
            modal.style.display = 'none';
            alert('Turno eliminado correctamente');
          }
        } catch (error) {
          console.error('Error en el proceso de eliminación:', error);
          alert('Error al eliminar el turno: ' + (error.message || 'Por favor intente nuevamente'));
        }
      }
    };
  }
}

document.addEventListener('DOMContentLoaded', initAdmin);