// Inicialización
document.addEventListener('DOMContentLoaded', () => {

  // Verificar si hay usuario en LocalStorage
  const usuarioLS = localStorage.getItem('usuario');
  if (usuarioLS) {
    const user = JSON.parse(usuarioLS);
    actualizarUIUsuario(user);
  }

  if (document.querySelector('.contenedor-pasos')) {
    probarConexion();
    inicializarEventos();
    actualizarInterfaz();
    inicializarAuth();
    inicializarDisponibilidad();
  }

  document.addEventListener('wheel', function(e) {
        const pasoActivo = document.querySelector('.paso.activo');
        if (pasoActivo && pasoActivo.scrollHeight > pasoActivo.clientHeight) {
            // Prevenir el scroll por defecto
            e.preventDefault();
            
            // Aplicar el scroll al contenedor activo
            pasoActivo.scrollTop += e.deltaY;
        }
    }, { passive: false });
});

function mostrarError(mensaje) {
    document.querySelectorAll('.modal-error, .modal-overlay').forEach(el => el.remove());
    
    const overlay = document.createElement('div');
    overlay.className = 'modal-overlay';
    
    const modal = document.createElement('div');
    modal.className = 'modal-error';
    
    let mensajePersonalizado = mensaje;
    
    // Detectar conflictos de horario
    if (mensaje.includes('minutos') || mensaje.includes('horario')) {
        const duracionTotal = calcularDuracionTotal();
        const horas = Math.floor(duracionTotal / 60);
        const minutos = duracionTotal % 60;
        
        const textoDuracion = horas > 0 
            ? `${horas} hora${horas > 1 ? 's' : ''}${minutos > 0 ? ` y ${minutos} minutos` : ''}`
            : `${minutos} minutos`;
        
        mensajePersonalizado = `
            <svg viewBox="0 0 24 24" width="48" height="48" style="margin-bottom: 1rem;">
                <path fill="#ff6b6b" d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm1 15h-2v-2h2v2zm0-4h-2V7h2v6z"/>
            </svg>
            <h3>¡Cruce de horarios!</h3>
            <p>
                Tu turno necesita <strong>${textoDuracion}</strong> como mínimo.
                <br><br>
                Por favor, elegí otro horario donde entren todos tus servicios.
            </p>
        `;
    }
    
    modal.innerHTML = `
        ${mensajePersonalizado}
        <button id="cerrar-modal">¡Entendido!</button>
    `;
    
    // Agregar al DOM
    document.body.appendChild(overlay);
    document.body.appendChild(modal);
    
    // Cerrar al hacer clic
    const cerrarModal = () => {
        modal.style.animation = 'fadeIn 0.3s reverse';
        overlay.style.animation = 'fadeIn 0.3s reverse';
        setTimeout(() => {
            modal.remove();
            overlay.remove();
        }, 300);
    };
    
    modal.querySelector('#cerrar-modal').addEventListener('click', cerrarModal);
    overlay.addEventListener('click', cerrarModal);
}

// Funciones principales
function inicializarEventos() {
    // Navegación
    elementos.btnAnterior.addEventListener('click', retrocederPaso);
    elementos.btnSiguiente.addEventListener('click', avanzarPaso);
    
    // Selección de servicios (manejo delegado)
    document.querySelectorAll('.flotante.servicio').forEach(servicio => {
        servicio.addEventListener('click', function() {
            this.classList.toggle('seleccionado');
            actualizarServiciosSeleccionados();
        });
    });
    
    // Fechas y horarios
    document.querySelectorAll('.flotante.fecha').forEach(fecha => {
        fecha.addEventListener('click', function() {
            seleccionarElemento(this, 'fecha');
        });
    });
    
    document.querySelectorAll('.flotante.horario').forEach(horario => {
        horario.addEventListener('click', async function() {
            if (!this.classList.contains('ocupado')) {
            const horarioSeleccionado = this.querySelector('.p1').textContent;
            const validacion = await validarDisponibilidadCompleta(
                datosReserva.fecha, 
                horarioSeleccionado
            );
            
            if (validacion.disponible && validacion.turnosSolapados.length === 0) {
                seleccionarElemento(this, 'horario');
            } else {
                mostrarError(validacion.mensaje || "Horario no disponible");
            }
            }
        });
    });
    
    // Validación en tiempo real
    document.getElementById('nombre')?.addEventListener('input', validarNombre);
    document.getElementById('email')?.addEventListener('input', validarEmail);
    document.getElementById('telefono')?.addEventListener('input', validarTelefono);
}

function calcularSubtotal() {
    const subtotal = datosReserva.servicios.reduce((total, servicio) => total + servicio.precio, 0);
    document.getElementById('subtotal').textContent = `$${subtotal.toLocaleString()}`;
    return subtotal;
}

function actualizarServiciosSeleccionados() {
    const serviciosSeleccionados = document.querySelectorAll('.flotante.servicio.seleccionado');
    datosReserva.servicios = Array.from(serviciosSeleccionados).map(serv => {
        return {
            nombre: serv.getAttribute('data-servicio'),
            precio: datosReserva.precios[serv.getAttribute('data-servicio')],
            duracion: datosReserva.duraciones[serv.getAttribute('data-servicio')]
        };
    });
    
    // Actualizar contador
    document.getElementById('contador-servicios').textContent = datosReserva.servicios.length;
    
    // Ocultar error si hay selección
    if (datosReserva.servicios.length > 0) {
        document.getElementById('error-servicio').classList.remove('visible');
    }
    
    // Calcular y mostrar subtotal
    calcularSubtotal();
}

function calcularDuracionTotal() {
    return datosReserva.servicios.reduce((total, servicio) => total + servicio.duracion, 0);
}

async function avanzarPaso() {
    if (pasoActual >= totalPasos) return;
    
    try {
        // Mostrar spinner
        toggleLoader(true);
        
        const pasoValido = await validarPaso(pasoActual);
        if (!pasoValido) {
            toggleLoader(false);
            return;
        }

        pasoActual++;
        actualizarInterfaz();
        
        if (pasoActual === 4 && usuarioActual) {
            autocompletarFormulario(usuarioActual);
        }
        
        if (pasoActual === 5) {
            guardarDatosFormulario();
            actualizarResumen();
        }
    } catch (error) {
        console.error('Error al avanzar paso:', error);
        mostrarError('Ocurrió un error al validar los datos');
    } finally {
        // Ocultar spinner siempre, tanto en éxito como en error
        toggleLoader(false);
    }
}

function retrocederPaso() {
    if (pasoActual > 1) {
        switch (pasoActual) {
            case 2: // Si estamos en fechas y retrocedemos a servicios
                // No necesitamos limpiar nada específico
                break;
                
            case 3: // Si estamos en horarios y retrocedemos a fechas
                limpiarSeleccion('horario');
                datosReserva.horario = null;
                break;
                
            case 4:
                break;
                
            case 5:
                break;
        }
        
        pasoActual--;
        actualizarInterfaz();
    }
}

function limpiarSeleccion(tipo) {
    document.querySelectorAll(`.flotante.${tipo}.seleccionado`).forEach(elemento => {
        elemento.classList.remove('seleccionado');
    });
    
    const errorElement = document.getElementById(`error-${tipo}`);
    if (errorElement) {
        errorElement.classList.remove('visible');
    }
}

function actualizarInterfaz() {
    actualizarPasos();
    actualizarProgreso();
    actualizarBotones();
}

async function simularReserva() {
  if (pasoActual === totalPasos) {
        const loader = document.getElementById('reserva-loader');
        const btnSiguiente = document.querySelector('.btn-siguiente');
        const duracionTotal = calcularDuracionTotal();

        btnSiguiente.classList.add('hidden');
        setTimeout(() => {
            loader.style.display = 'inline-block';
        }, 200);

        // Validación completa
        const validacion = await validarDisponibilidadCompleta(
            datosReserva.fecha, 
            datosReserva.horario,
            duracionTotal
        );

        if (!validacion.disponible) {
            requestAnimationFrame(() => {
                loader.style.display = 'none';
                btnSiguiente.style.display = 'flex';
            });
            
            console.error('Validación fallida:', validacion);
            mostrarError(validacion.mensaje || "El horario ya no está disponible");
            return;
        }

    try {
      // Validar disponibilidad
      const disponible = await firestoreDB.verificarDisponibilidad(
        datosReserva.fecha, 
        datosReserva.horario
      );
      
      if (!disponible) {
        mostrarConfirmacion(false);
        return;
      }
  
      // Crear objeto de turno
      const turnoData = {
        cliente: datosReserva.nombre,
        email: datosReserva.email,
        telefono: datosReserva.telefono,
        servicios: datosReserva.servicios.map(s => s.nombre),
        fecha: datosReserva.fecha,
        horario: datosReserva.horario,
        montoTotal: calcularSubtotal(),
        reserva: datosReserva.reserva,
        observaciones: datosReserva.observacion || '',
        userId: usuarioActual?.uid || null,
        duracion: duracionTotal
      };
  
      // Guardar en Firestore
      await firestoreDB.guardarTurno(turnoData);
      
      // Enviar email de confirmación (no esperamos respuesta para no bloquear la UI)
      enviarEmailConfirmacion({
        ...turnoData,
        nombre: datosReserva.nombre,
        servicios: datosReserva.servicios.map(s => s.nombre)
      }).then(enviado => {
        if (!enviado) {
          console.log("El email no pudo enviarse, pero el turno fue reservado");
        }
      });

      // Ocultar spinner después de la reserva exitosa
      loader.style.display = 'none';
      
      // Mostrar confirmación
      mostrarConfirmacion(true);
      
    } catch (error) {
      console.error('Error al reservar:', error);
      // Restaurar UI si hay error
      requestAnimationFrame(() => {
        loader.style.display = 'none';
        btnSiguiente.style.display = 'flex'; // Usar display original del botón
      });
      mostrarConfirmacion(false);
    }
  }
}

async function completarReserva() {
  let bloqueoId = null;
  
  try {
    // 1. Crear bloqueo (con ID automático)
    bloqueoId = await firestoreDB.bloquearTurnoTemporal(
      datosReserva.fecha,
      datosReserva.horario,
      usuarioActual?.uid || 'anonimo'
    );

    // 2. Validación final
    const config = await firestoreDB.obtenerConfiguracion();
    const [disponible, turnosSolapados] = await Promise.all([
      firestoreDB.verificarDisponibilidad(datosReserva.fecha, datosReserva.horario),
      firestoreDB.obtenerTurnosSolapados(
        datosReserva.fecha, 
        datosReserva.horario,
        config.duracionTurno
      )
    ]);

    if (!disponible || turnosSolapados.length > 0) {
      await firestoreDB.eliminarBloqueoTemporal(bloqueoId);
      mostrarError(turnosSolapados.length > 0 
        ? `El horario se solapa con un turno existente (${turnosSolapados[0].servicios.join(', ')})`
        : "El horario ya fue reservado");
      return;
    }

    // 3. Guardar turno
    const turnoData = { /* ... tus datos de turno ... */ };
    await firestoreDB.guardarTurno(turnoData);

    // 4. Limpiar bloqueo
    await firestoreDB.eliminarBloqueoTemporal(bloqueoId);

    mostrarConfirmacion(true);
  } catch (error) {
    console.error("Error en reserva:", error);
    if (bloqueoId) {
      await firestoreDB.eliminarBloqueoTemporal(bloqueoId).catch(e => console.error("Error limpiando bloqueo:", e));
    }
    mostrarConfirmacion(false);
  }
}

