async function validarDisponibilidadCompleta(fecha, horario, duracion) {
    try {
        const config = await firestoreDB.obtenerConfiguracion();
        
        // Convertir horario de inicio a minutos
        const [horaStr, minutoStr] = horario.split(':');
        const horaInicio = parseInt(horaStr);
        const minutoInicio = parseInt(minutoStr);
        const inicioMinutos = horaInicio * 60 + minutoInicio;
        const finMinutos = inicioMinutos + duracion;
        
        // Obtener todos los turnos del día
        const turnosDelDia = await firestoreDB.obtenerTurnosPorFecha(fecha);
        
        // Verificar solapamientos
        const turnosSolapados = turnosDelDia.filter(turno => {
            const [turnoHora, turnoMinuto] = turno.horario.split(':').map(Number);
            const turnoInicioMin = turnoHora * 60 + turnoMinuto;
            const turnoFinMin = turnoInicioMin + (turno.duracion || 30);
            
            // Verificar solapamiento
            return !(finMinutos <= turnoInicioMin || inicioMinutos >= turnoFinMin);
        });
        
        // Verificar disponibilidad de cada slot individual
        const slotsOcupados = [];
        for (let min = inicioMinutos; min < finMinutos; min += 30) {
            const hora = Math.floor(min / 60);
            const minuto = min % 60;
            const horarioSlot = `${hora.toString().padStart(2, '0')}:${minuto.toString().padStart(2, '0')}`;
            
            const disponible = await firestoreDB.verificarDisponibilidad(fecha, horarioSlot);
            if (!disponible) {
                slotsOcupados.push(horarioSlot);
            }
        }
        
        const disponible = turnosSolapados.length === 0 && slotsOcupados.length === 0;
        
        // console.log(`Validación completa para ${horario} (${duracion} min):`, {
        //     disponible,
        //     turnosSolapados: turnosSolapados.map(t => t.horario),
        //     slotsOcupados
        // });

        let mensaje = '';
        if (!disponible) {
            const horas = Math.floor(duracion / 60);
            const minutos = duracion % 60;
            
            mensaje = `Tu turno necesita ${horas > 0 ? horas + ' hora' + (horas > 1 ? 's' : '') + ' y ' : ''}${minutos} minutos continuos.`;
        }
        
        return {
            disponible,
            turnosSolapados,
            slotsOcupados,
            mensaje
        };
    } catch (error) {
        console.error("Error en validación:", error);
        return {
            disponible: false,
            turnosSolapados: [],
            slotsOcupados: [],
            mensaje: "Error al verificar disponibilidad. Por favor, intenta nuevamente."
        };
    }
}

// Nueva función para generar fechas disponibles
function generarFechasDisponibles(config) {
  // Obtener fecha y hora actual en Argentina (UTC-3)
  const ahoraArg = new Date(new Date().toLocaleString("en-US", {timeZone: "America/Argentina/Buenos_Aires"}));
  const hoyArg = new Date(ahoraArg.getFullYear(), ahoraArg.getMonth(), ahoraArg.getDate());
  
  // Verificar si ya pasó el horario de cierre (usando hora Argentina)
  const [horaCierre, minCierre] = config.horarioCierre.split(':').map(Number);
  const empezarDesdeManana = ahoraArg.getHours() > horaCierre || 
                       (ahoraArg.getHours() === horaCierre && ahoraArg.getMinutes() >= minCierre);

  const fechasDisponibles = [];
  const DIAS_A_MOSTRAR = 7;
  
  // Comenzar desde hoy o mañana según horario
  const diaInicial = empezarDesdeManana ? 1 : 0;
  
  for (let i = diaInicial; i < DIAS_A_MOSTRAR + diaInicial; i++) {
    const fecha = new Date(hoyArg);
    fecha.setDate(hoyArg.getDate() + i);
    
    // Verificar día laboral (usando fecha Argentina)
    if (!esDiaLaboral(fecha, config)) {
      // console.log(`Saltando ${fecha.toDateString()} - No es día laboral`);
      continue;
    }
    
    const fechaStr = fecha.toISOString().split('T')[0];
    
    // Verificar días no laborables
    if (Array.isArray(config.diasNoLaborables) && config.diasNoLaborables.includes(fechaStr)) {
      // console.log(`Saltando ${fechaStr} - Día no laborable configurado`);
      continue;
    }
    
    // No agregar hoy si ya pasó el horario de cierre
    if (i === 0 && empezarDesdeManana) continue;
    
    fechasDisponibles.push({
      fecha: fechaStr,
      display: formatearFechaDisplay(fecha)
    });
  }
  
  // console.log("Fechas disponibles generadas:", fechasDisponibles);
  return fechasDisponibles;
}

function esDiaLaboral(fecha, config) {
  const diaSemana = fecha.getDay(); // 0=Domingo, 1=Lunes, etc.
  
  // Asegurarnos que diasLaborales es un array de números válidos
  let diasLaborales = [];
  
  try {
    if (Array.isArray(config.diasLaborales)) {
      diasLaborales = config.diasLaborales.map(Number).filter(n => !isNaN(n));
    } else if (typeof config.diasLaborales === 'string') {
      diasLaborales = config.diasLaborales.split(',').map(Number).filter(n => !isNaN(n));
    }
  } catch (e) {
    console.error("Error procesando días laborales:", e);
    diasLaborales = [1,2,3,4,5]; // Valor por defecto: Lunes a Viernes
  }
  
  // Si no hay días definidos, usar valor por defecto
  if (diasLaborales.length === 0) {
    diasLaborales = [1,2,3,4,5];
  }
  
  return diasLaborales.includes(diaSemana);
}

function formatearFechaDisplay(fecha) {
  return fecha.toLocaleDateString('es-AR', {
    weekday: 'long',
    day: 'numeric',
    month: 'short'
  });
}

function obtenerFechaSeleccionada() {
  // Asume que la fecha está en formato "Lunes 9/01" o similar
  // Necesitas convertir esto a un formato que coincida con tu DB (YYYY-MM-DD)
  if (!datosReserva.fecha) return new Date().toISOString().split('T')[0];
  
  // Implementación básica - ajusta según tu formato de fecha
  const partes = datosReserva.fecha.split(' ');
  const [dia, mes] = partes[1].split('/');
  const año = new Date().getFullYear();
  
  return `${año}-${mes.padStart(2, '0')}-${dia.padStart(2, '0')}`;
}

function actualizarUIFechas(fechasDisponibles) {
  const paso2 = document.querySelector('.paso-2');
  if (!paso2) {
    console.error('No se encontró el contenedor del paso 2');
    return;
  }

  const contenedorFechas = paso2.querySelector('.contenedor-fechas');
  const plantillaFecha = paso2.querySelector('.flotante.fecha');
  const cargando = paso2.querySelector('.cargando-fechas');
  const sinFechas = paso2.querySelector('.sin-fechas');

  // Ocultar mensaje de carga y limpiar contenedor
  if (cargando) cargando.style.display = 'none';
  if (sinFechas) sinFechas.style.display = 'none';
  contenedorFechas.innerHTML = '';

  if (plantillaFecha) {
    plantillaFecha.style.display = 'none';
  }

  if (fechasDisponibles.length === 0) {
    if (sinFechas) {
      sinFechas.style.display = 'block';
      sinFechas.textContent = 'No hay fechas disponibles en este momento';
    }
    return;
  }

  // Mostrar fechas disponibles
  contenedorFechas.style.display = 'grid';
  fechasDisponibles.forEach(fecha => {
    if (!plantillaFecha) return;
    
    const nuevoElemento = plantillaFecha.cloneNode(true);
    nuevoElemento.style.display = 'flex';
    nuevoElemento.querySelector('.p1').textContent = fecha.display;
    nuevoElemento.dataset.fechaReal = fecha.fecha;
    
    nuevoElemento.addEventListener('click', function() {
      seleccionarElemento(this, 'fecha');
      cargarHorariosParaFecha(fecha.fecha);
    });
    
    contenedorFechas.appendChild(nuevoElemento);
  });
}

async function cargarHorariosParaFecha(fecha) {
  const paso3 = document.querySelector('.paso-3');
  if (!paso3) return;

  // Resetear estado primero
  const contenedor = paso3.querySelector('.contenedor-horarios');
  const sinHorarios = paso3.querySelector('.sin-horarios');
  
  if (contenedor) contenedor.innerHTML = '';
  if (sinHorarios) sinHorarios.style.display = 'none';

  // Mostrar carga
  const cargando = paso3.querySelector('.cargando-horarios');
  if (cargando) cargando.style.display = 'block';

  try {
    // console.log("Cargando horarios para fecha:", fecha);
    let horarios = await firestoreDB.generarHorariosDisponibles(fecha);
    // console.log("Horarios recibidos:", horarios);
    
    // Obtener fecha actual en Argentina (UTC-3)
    const ahoraArg = new Date(new Date().toLocaleString("en-US", {timeZone: "America/Argentina/Buenos_Aires"}));
    const hoyArg = new Date(ahoraArg.getFullYear(), ahoraArg.getMonth(), ahoraArg.getDate());
    const fechaSeleccionada = new Date(fecha + 'T00:00:00-03:00'); // Forzar zona horaria Argentina
    
    // Si es hoy, filtrar los horarios que ya pasaron o están muy próximos
    if (fechaSeleccionada.getTime() === hoyArg.getTime()) {
      const ahoraEnMinutos = ahoraArg.getHours() * 60 + ahoraArg.getMinutes();
      const tiempoMinimoReserva = 30; // 30 minutos de anticipación
      
      horarios = horarios.filter(horario => {
        const [horas, minutos] = horario.hora.split(':').map(Number);
        const horarioEnMinutos = horas * 60 + minutos;
        
        // Solo mostrar horarios que tengan al menos 30 minutos de diferencia
        return horarioEnMinutos >= ahoraEnMinutos + tiempoMinimoReserva;
      });
      
      // console.log("Horarios filtrados para hoy:", horarios);
    }
    
    // Guardar fecha seleccionada
    datosReserva.fecha = fecha;
    
    // Actualizar UI
    actualizarUIHorarios(horarios);
    
  } catch (error) {
    console.error("Error cargando horarios:", error);
    if (sinHorarios) {
      sinHorarios.style.display = 'block';
      sinHorarios.textContent = 'Error al cargar horarios. Intenta nuevamente.';
    }
  } finally {
    if (cargando) cargando.style.display = 'none';
    if (contenedor) contenedor.style.display = 'grid';
  }
}

async function generarHorariosDisponibles(fecha) {
  const [config, turnosOcupados] = await Promise.all([
    this.obtenerConfiguracion(),
    this.obtenerTurnosPorFecha(fecha)
  ]);
  
  const horariosOcupados = turnosOcupados.map(t => t.horario);
  const horarios = this._generarHorarios(config, horariosOcupados, fecha);
  
  // console.log("Horarios generados para", fecha, ":", horarios);
  return horarios;
}

function actualizarUIHorarios(horariosDisponibles) {
  const paso3 = document.querySelector('.paso-3');
  if (!paso3) return;

  const contenedor = paso3.querySelector('.contenedor-horarios');
  const plantilla = paso3.querySelector('.flotante.horario');
  const sinHorarios = paso3.querySelector('.sin-horarios');
  
  // Limpiar contenedor completamente
  if (contenedor) contenedor.innerHTML = '';
  if (sinHorarios) sinHorarios.style.display = 'none';

  // Mostrar mensaje si no hay horarios
  if (!horariosDisponibles || horariosDisponibles.length === 0) {
    if (sinHorarios) {
      sinHorarios.style.display = 'block';
      sinHorarios.textContent = 'No hay horarios disponibles para esta fecha';
    }
    return;
  }

  // Crear elementos de horarios
  horariosDisponibles.forEach(horario => {
    const elemento = document.createElement('div');
    elemento.className = 'flotante horario';
    elemento.style.display = 'flex';
    
    // Crear elementos internos
    const p1 = document.createElement('p');
    p1.className = 'p1';
    p1.textContent = horario.hora;
    
    const svg = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
    svg.setAttribute('class', 'check-icon');
    svg.setAttribute('viewBox', '0 0 24 24');
    
    const path = document.createElementNS('http://www.w3.org/2000/svg', 'path');
    path.setAttribute('d', 'M9 16.17L4.83 12l-1.42 1.41L9 19 21 7l-1.41-1.41z');
    
    svg.appendChild(path);
    
    // Ensamblar el elemento
    elemento.appendChild(p1);
    elemento.appendChild(svg);
    
    // Solo agregar la clase .ocupado si no está disponible
    if (!horario.disponible) {
      elemento.classList.add('ocupado');
    } else {
      elemento.addEventListener('click', () => {
        seleccionarElemento(elemento, 'horario');
        datosReserva.horario = horario.hora;
      });
    }
    
    if (contenedor) contenedor.appendChild(elemento);
  });
}

async function verificarDisponibilidadEnTiempoReal(fecha, horario) {
  try {
    const disponible = await firestoreDB.verificarDisponibilidad(fecha, horario);
    if (!disponible) {
      mostrarError('Este horario ya no está disponible. Por favor selecciona otro.');
      // Remover el horario ocupado de la UI
      document.querySelectorAll('.flotante.horario').forEach(el => {
        if (el.textContent.trim() === horario) {
          el.classList.add('ocupado');
          el.removeEventListener('click', manejarClickHorario);
        }
      });
      return false;
    }
    return true;
  } catch (error) {
    console.error('Error verificando disponibilidad:', error);
    return false;
  }
}

