// firestore.js - Versión completa con validación cruzada
(() => {
  if (!firebase.apps.length) {
    throw new Error('Firebase no está inicializado');
  }

  const { db } = window;

  window.firestoreDB = {
    // Obtener configuración
    async obtenerConfiguracion() {
      const doc = await db.collection('config').doc('horarios').get();
      if (!doc.exists) throw new Error("No se encontró configuración");
      
      const data = doc.data();
      
      // Limpiar formatos de horarios
      if (typeof data.horarioApertura === 'string') {
        data.horarioApertura = data.horarioApertura.replace(/"/g, '');
      }
      if (typeof data.horarioCierre === 'string') {
        data.horarioCierre = data.horarioCierre.replace(/"/g, '');
      }
      
      // Convertir diasLaborales a array de números
      if (Array.isArray(data.diasLaborales)) {
        if (data.diasLaborales.length === 1 && typeof data.diasLaborales[0] === 'string') {
          // Caso: ['[1,2,3,4,5]']
          try {
            data.diasLaborales = JSON.parse(data.diasLaborales[0]);
          } catch (e) {
            console.error("Error parseando diasLaborales:", e);
            data.diasLaborales = [1,2,3,4,5]; // Valor por defecto (L-V)
          }
        }
      } else if (typeof data.diasLaborales === 'string') {
        // Caso: "1,2,3,4,5"
        data.diasLaborales = data.diasLaborales.split(',').map(Number).filter(n => !isNaN(n));
      }
      
      // Convertir diasNoLaborables si es necesario
      if (typeof data.diasNoLaborables === 'string') {
        try {
          data.diasNoLaborables = JSON.parse(data.diasNoLaborables);
        } catch (e) {
          data.diasNoLaborables = data.diasNoLaborables.replace(/[\[\]\s]/g, '').split(',').filter(Boolean);
        }
      }
      
      return data;
    },

    // Obtener turnos por fecha
    async obtenerTurnosPorFecha(fecha) {
      const snapshot = await db.collection('turnos')
        .where('fecha', '==', fecha)
        .get();
      return snapshot.docs.map(doc => doc.data());
    },

    // Generar horarios disponibles
    async generarHorariosDisponibles(fecha) {
        const [config, turnosOcupados] = await Promise.all([
        this.obtenerConfiguracion(),
        this.obtenerTurnosPorFecha(fecha)
        ]);
        
        // Crear array de todos los slots ocupados, incluyendo los extendidos
        const slotsOcupados = [];
        turnosOcupados.forEach(turno => {
            const [hora, minuto] = turno.horario.split(':').map(Number);
            const inicioMinutos = hora * 60 + minuto;
            const finMinutos = inicioMinutos + (turno.duracion || 30);
            
            for (let min = inicioMinutos; min < finMinutos; min += 30) {
                const horaSlot = Math.floor(min / 60);
                const minutoSlot = min % 60;
                const horarioSlot = `${horaSlot.toString().padStart(2, '0')}:${minutoSlot.toString().padStart(2, '0')}`;
                slotsOcupados.push(horarioSlot);
            }
        });
        
        // console.log(`Slots ocupados para ${fecha}:`, slotsOcupados);
        
        return this._generarHorarios(config, slotsOcupados, fecha);
    },

    // Función privada para generar horarios
    _generarHorarios(config, slotsOcupados, fechaSeleccionada) {
      const horarioApertura = config.horarioApertura;
      const horarioCierre = config.horarioCierre;
      
      // Asegurar formato correcto (por si hay puntos en lugar de dos puntos)
      const horaApertura = parseInt(horarioApertura.replace('.', ':').split(':')[0]);
      const minApertura = parseInt(horarioApertura.replace('.', ':').split(':')[1] || 0);
      const horaCierre = parseInt(horarioCierre.replace('.', ':').split(':')[0]);
      const minCierre = parseInt(horarioCierre.replace('.', ':').split(':')[1] || 0);
      const duracion = config.duracionTurno;

      // Validación de valores numéricos
      if (isNaN(horaApertura)) throw new Error('Horario apertura inválido');
      if (isNaN(minApertura)) minApertura = 0;
      if (isNaN(horaCierre)) throw new Error('Horario cierre inválido');
      if (isNaN(minCierre)) minCierre = 0;
      if (isNaN(duracion)) throw new Error('Duración turno inválida');

      const horarios = [];
      let horaActual = horaApertura;
      let minActual = minApertura;

      while (horaActual < horaCierre || (horaActual === horaCierre && minActual <= minCierre)) {
        const horarioStr = `${horaActual.toString().padStart(2, '0')}:${minActual.toString().padStart(2, '0')}`;
        
        const ocupado = slotsOcupados.includes(horarioStr);

        horarios.push({
          hora: horarioStr,
          disponible: !ocupado
        });

        // Avanzar el tiempo
        minActual += duracion;
        if (minActual >= 60) {
          horaActual += Math.floor(minActual / 60);
          minActual = minActual % 60;
        }
      }

      // Validación mínima de resultados
      if (horarios.length === 0) {
        console.error('No se generaron horarios con la configuración:', config);
        throw new Error('No se pudieron generar horarios');
      }

      // console.log(`Horarios generados para ${fechaSeleccionada}:`, horarios);
      return horarios;
    },

    // Verificar disponibilidad básica
    async verificarDisponibilidad(fecha, horario) {
          try {
            const snapshot = await db.collection('turnos')
                .where('fecha', '==', fecha)
                .where('horario', '==', horario)
                .limit(1)
                .get();
                
            return snapshot.empty;
        } catch (error) {
            console.error("Error verificando disponibilidad:", error);
            return false;
        }
    },
    
    // Verificar solapamientos
    async obtenerTurnosSolapados(fecha, horario, duracion) {
      const [horaStr, minutoStr] = horario.split(':');
      const horaInicio = parseInt(horaStr);
      const minutoInicio = parseInt(minutoStr);
      
      const minutoFin = minutoInicio + duracion;
      const horaFin = horaInicio + Math.floor(minutoFin / 60);
      const minutoFinAjustado = minutoFin % 60;
      const horarioFin = `${horaFin.toString().padStart(2, '0')}:${minutoFinAjustado.toString().padStart(2, '0')}`;
      
      const snapshot = await db.collection('turnos')
        .where('fecha', '==', fecha)
        .where('horario', '>=', horario)
        .where('horario', '<', horarioFin)
        .get();
        
      return snapshot.docs.map(doc => doc.data());
    },

    // Bloqueo temporal
    async bloquearTurnoTemporal(fecha, horario, userId) {
      const docRef = await db.collection('bloqueos_temporales').add({
        fecha,
        horario,
        userId,
        timestamp: firebase.firestore.FieldValue.serverTimestamp(),
        expiresAt: new Date(Date.now() + 5*60*1000) // 5 minutos de expiración
      });
      return docRef.id; // Devolver el ID automático
    },

    async eliminarBloqueoTemporal(bloqueoId) {
      await db.collection('bloqueos_temporales').doc(bloqueoId).delete();
    },

    // Guardar turno
    async guardarTurno(turnoData) {
      try {
        // Calcular duración total si no viene en los datos
        if (!turnoData.duracion && turnoData.servicios) {
            const duraciones = {
                "Corte": 30,
                "Teñido": 30,
                "Alisado": 30
            };
            
            turnoData.duracion = turnoData.servicios.reduce((total, servicio) => {
                return total + (duraciones[servicio] || 30);
            }, 0);
            
            // console.log(`Duración calculada para turno: ${turnoData.duracion} minutos`);
        }
        
        // Convertir horario de inicio a minutos
        const [horaInicioStr, minutoInicioStr] = turnoData.horario.split(':');
        const horaInicio = parseInt(horaInicioStr);
        const minutoInicio = parseInt(minutoInicioStr);
        const inicioMinutos = horaInicio * 60 + minutoInicio;
        
        // Calcular horario de fin
        const finMinutos = inicioMinutos + turnoData.duracion;
        
        // Crear array de todos los slots que ocupará este turno
        const slotsOcupados = [];
        for (let min = inicioMinutos; min < finMinutos; min += 30) {
            const hora = Math.floor(min / 60);
            const minuto = min % 60;
            const horario = `${hora.toString().padStart(2, '0')}:${minuto.toString().padStart(2, '0')}`;
            slotsOcupados.push(horario);
        }
        
        // console.log(`Este turno ocupará los siguientes slots: ${slotsOcupados.join(', ')}`);
        
        // Guardar el turno principal
        const docRef = await db.collection('turnos').add({
            ...turnoData,
            slotsOcupados: slotsOcupados, // Guardar todos los slots ocupados
            estado: 'pendiente',
            createdAt: firebase.firestore.FieldValue.serverTimestamp(),
            actualizadoEl: firebase.firestore.FieldValue.serverTimestamp()
        });
        
        // console.log(`Turno guardado exitosamente. ID: ${docRef.id}`);
        return { id: docRef.id, ...turnoData };
        } catch (error) {
            console.error("Error guardando turno:", error);
            throw error;
        }
    }
  };
})();